import AV from 'leancloud-storage'

let APP_ID = 'Rurjim7Hgz5o4c2pLEd5IKvu-gzGzoHsz';
let APP_KEY = 'fF5yjVpcDkrDoOeXX59z7UPl';
AV.init({appId: APP_ID, appKey: APP_KEY});

export const TodoModel = {

    getByUser(user, successFn, errorFn) {
        //  https://leancloud.cn/docs/leanstorage_guide-js.html#
        let query = new AV.Query('Todo');
        query.find().then((response) => {
            let array = response.map((t) => {
                return {id: t.id, ...t.attributes}
            });
            successFn.call(null, array)
        }, (error) => {
            errorFn && errorFn.call(null, error)
        });
    },

    /*todo
    * https://leancloud.cn/docs/leanstorage_guide-js.html*/

    create({status, title, deleted}, successFn, errorFn) {
        let Todo = AV.Object.extend('Todo');
        let todo = new Todo();
        todo.set('title', title);
        todo.set('status', status);
        todo.set('deleted', deleted);

        //  https://leancloud.cn/docs/acl-guide.html#
        // Todo 
        let acl = new AV.ACL();
        acl.setPublicReadAccess(false);//
        acl.setWriteAccess(AV.User.current(), true);
        acl.setReadAccess(AV.User.current(), true);

        todo.setACL(acl);

        todo.save().then(function (response) {
            successFn.call(null, response.id)
        }, function (error) {
            errorFn && errorFn.call(null, error)
        });
    },
    update({id, title, status, deleted}, successFn, errorFn) {
        //  https://leancloud.cn/docs/leanstorage_guide-js.html#
        let todo = AV.Object.createWithoutData('Todo', id);
        title !== undefined && todo.set('title', title);
        status !== undefined && todo.set('status', status);
        deleted !== undefined && todo.set('deleted', deleted);
        // 
        // 
        // update({id:1, title:'hi'})
        //  update  status  deleted
        // 
        //  set  set 
        //  title&&todo.set('title', title),undefined
        // 
        // update({id:1, title: '', status: null}}
        //  title  status 
        todo.save().then((response) => {
            successFn && successFn.call(null)
        }, (error) => errorFn && errorFn.call(null, error))
    },

    /*todo
     https://leancloud.cn/docs/leanstorage_guide-js.html#*/

    destroy(todoId, successFn, errorFn) {
        let todo = AV.Object.createWithoutData('Todo', todoId);
        todo.destroy().then(function (response) {
            successFn && successFn.call(null)
        }, function (error) {
            errorFn && errorFn.call(null, error)
        });
    }
};

/*
* https://leancloud.cn/docs/leanstorage_guide-js.html#*/

export function signIn(username, password, successFn, errorFn) {
    AV.User.logIn(username, password).then(function (loginedUser) {
        let user = getUserFromAVUser(loginedUser);
        successFn.call(null, user)
    }, function (error) {
        errorFn.call(null, error)
    })
}

/*
* https://leancloud.cn/docs/leanstorage_guide-js.html#*/

export function signUp(email, username, password, successFn, errorFn) {
    //  AVUser 
    var user = new AV.User();
    // 
    user.setUsername(username);
    // 
    user.setPassword(password);
    // 
    user.setEmail(email);
    user.signUp().then(function (loginedUser) {
        let user = getUserFromAVUser(loginedUser);
        successFn.call(null, user)
    }, function (error) {
        errorFn.call(null, error)
    });
    return undefined
}

/*
* 
* https://leancloud.cn/docs/leanstorage_guide-js.html#*/

export function sendPasswordResetEmail(email, successFn, errorFn) {
    AV.User.requestPasswordReset(email).then(function (success) {
        successFn.call(null,success);
    }, function (error) {
        errorFn.call(null, error)
    })
}

/*
* 
* https://leancloud.cn/docs/leanstorage_guide-js.html# */

export function getCurrentUser() {
    let user = AV.User.current();
    if (user) {
        return getUserFromAVUser(user)
    } else {
        return null
    }
}

/**/

function getUserFromAVUser(AVUser) {
    return {
        id: AVUser.id,
        ...AVUser.attributes
    }
}


/*
* 
* https://leancloud.cn/docs/leanstorage_guide-js.html#
* undefined*/

export function signOut() {
    AV.User.logOut();
    return undefined
}

export default AV


