import React, {Component} from 'react';
import '../../css/UserDialog.css'
import {signUp, signIn, sendPasswordResetEmail} from './leanCloud'
import SignInOrSignUp from './SignInOrSignUp'
import ForgotPasswordForm from './ForgotPasswordForm'

import $ from 'jquery';

window.jQuery = $;
require('semantic-ui/dist/semantic.min.css');
require('semantic-ui/dist/semantic.min.js');

export default class UserDialog extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'signInOrSignUp',
            formData: {
                email: '',
                username: '',
                password: '',
            },
            status_code: {
                1: '',
                125: '',
                202: '!',
                203: '!',
                204: '',
                205: '',
                210: '!',
                211: '!',
                219: '!',
                unknown: '!'
            },
            errorrecord: ''
        }
    }

    // dom 
    componentDidMount() {
        this.check(".ui.form");
    }


    //
    componentWillUpdate() {
    }

    /**/
    signUp(e) {
        e.preventDefault();// 
        let {email, username, password} = this.state.formData;
        let success = (user) => {
            this.props.onSignUp.call(null, user)
        };
        let error = (error) => {
            console.log(error);
            this.getErrorMessage(error.code);
            $('.ui.basic.modal').modal('show');
        };
        if (!this.state.formData.username || !this.state.formData.password || !this.state.formData.email) {
            return false;
        }
        /*leanCloud.js*/
        signUp(email, username, password, success, error)
    }

    /**/
    signIn(e) {
        e.preventDefault();
        let {username, password} = this.state.formData;
        let success = (user) => {
            this.props.onSignIn.call(null, user)
        };
        let error = (error) => {
            console.log(error);
            this.getErrorMessage(error.code);
            $('.ui.basic.modal').modal('show');
        };
        if (!this.state.formData.username || !this.state.formData.password) {
            return false;
        }
        signIn(username, password, success, error)
    }

    //
    getErrorMessage(code) {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.errorrecord = this.state.status_code[code] || this.state.status_code.unknown;
        this.setState(stateCopy);
    }

    changeFormData(key, e) {
        let stateCopy = JSON.parse(JSON.stringify(this.state)); //  JSON 
        stateCopy.formData[key] = e.target.value;
        this.setState(stateCopy)
    }

    render() {
        let center = 'center';
        return (
            <div>
                <div className="UserDialog-Wrapper">
                    <div className="UserDialog">
                        {this.state.selectedTab === 'signInOrSignUp'
                            ? <SignInOrSignUp
                                formData={this.state.formData}
                                onSignIn={this.signIn.bind(this)}
                                onSignUp={this.signUp.bind(this)}
                                onChange={this.changeFormData.bind(this)}
                                onForgotPassword={this.showForgotPassword.bind(this)}/>
                            : <ForgotPasswordForm
                                formData={this.state.formData}
                                onSubmit={this.resetPassword.bind(this)}
                                onChange={this.changeFormData.bind(this)}
                                onSignIn={this.returnToSignIn.bind(this)}/>}
                    </div>

                </div>
                <div className="ui basic modal">
                    <div className="ui icon header">
                        <i className="remove icon"></i> 
                    </div>
                    <div className="content">
                        <div className="description">
                            <p style={{textAlign: center}}>{this.state.errorrecord}</p>
                        </div>
                    </div>
                    <div className="actions">
                        <div className="ui green ok inverted button">
                            
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    showForgotPassword() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = 'forgotPassword';
        this.setState(stateCopy);
    }

    returnToSignIn() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = 'signInOrSignUp';
        this.setState(stateCopy);
    }

    //
    resetPassword(e) {
        e.preventDefault();
        let success = (success) => {
            alert(success)
        };
        let error = (error) => {
            console.log(error);
            this.getErrorMessage(error.code);
            $('.ui.basic.modal').modal('show');
        };
        sendPasswordResetEmail(this.state.formData.email, success, error);
    }

    //
    check(el) {
        $(el).form({
            fields: {
                email: {
                    identifier: 'email',
                    rules: [
                        {
                            type: 'email',
                            prompt: ''
                        }
                    ]
                },
                username: {
                    identifier: 'username',
                    rules: [
                        {
                            type: 'empty',
                            prompt: ''
                        }
                    ]
                },
                password: {
                    identifier: 'password',
                    rules: [
                        {
                            type: 'empty',
                            prompt: ''
                        },
                        {
                            type: 'minLength[6]',
                            prompt: ' {ruleValue} '
                        }
                    ]
                }
            },
            inline: true,
            on: 'blur'
        });
    }
}


