import React, {Component} from 'react';

export default class ForgotPasswordForm extends Component {
    render() {
        return (
            <div className="forgotPassword">
                <h3 className="horizontal divider"><i className="bar chart icon"></i></h3>

                <form className="ui form" onSubmit={this.props.onSubmit} id="defaultForm">
                    <div className="field">
                        <label></label>
                        <div className="ui left icon input">
                            <input type="text" placeholder="email" name="email" value={this.props.formData.email}
                                   onChange={this.props.onChange.bind(null, 'email')}/>
                            <i className="mail icon"></i>
                        </div>
                    </div>
                    <button className="ui fluid blue submit button" type="submit"></button>
                    <div className="right">
                    <a href="#" onClick={this.props.onSignIn}></a>
                    </div>
                </form>
            </div>
        )
    }
}


